module.exports = {"en":{"translation":{"site":{"title":"Jonathan Pontet"},"section1":{"headline":"What is <em>your</em> perspective","intro":"My solutions may be just a drop in the ocean, but it is my desire to make life just a little bit better, for as many people as possible that motivates me.<br>Its all about legacy"},"section2":{"headline":"Bedrock","intro":"The supporting foundations from which everything grows.<br>I have spent many years in education and industry cultivating a rich creative and technical skillset that allows me to both conceive and then create my ideas."},"section3":{"headline":"Digital","intro":"In the beginning, there was digital.<br>As I pursued higher education in the slipstream of the dot com boom, working in digital was always inevitable and I am as much absorbed in it today as I was then."},"section4":{"headline":"Analog","intro":"Going back to the beginning.<br>With digital comes motionless, so analog is my balance, providing harmony and connection with the world outside of digital the natural world."},"section5":{"headline":"Thereafter","intro":"Would you like to be in the next scene of my journey?<br>Send me a message telling me what we can create together that will make a difference."}}}}